.option-pane-container {
    background-color: #282c34;
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: 1;
}

.option-pane-container-off {
    display: none;
}

input{
    width:fit-content;
}

@media screen and (orientation: portrait) {
    
    h1{
        font-size: 5em;
        margin-top: 8%;
    }

    h2{
        margin-top: 2%;
    }

    .pools{
        text-align: left;
        margin-left: 8%;
        margin-top: 8%;
        width: 80%;
    }

    .other-settings{
        margin-left: 8%;
        margin-top: 3%;
        text-align: left;
    }

    #arrow-pic{
        width: 22%;
        position: absolute;
        top: 80%;
        left:10%;
    }

    #starting-die{
        cursor: pointer;
        width: 55%;
        position: absolute;
        top: 64%;
        left:36%;
    }
}

@media screen and (orientation: landscape) {
    
    h1{
        font-size: 3em;
        margin-top: 4%;
    }

    h2{
        margin-top: 0.5%;
        font-size: 0.95em;
    }

    .pools{
        font-size: 1.5em;
        text-align: left;
        position: absolute;
        left: 10%;
        top: 30%;
        width: 35%;
    }

    .other-settings{
        font-size: 1.5em;
        text-align: left;
        position: absolute;
        left: 60%;
        top: 30%;
        width: 35%;
    }

    #arrow-pic{
        width: 6%;
        position: absolute;
        top: 76%;
        left:54%;
    }

    #starting-die{
        cursor: pointer;
        width: 20%;
        position: absolute;
        top: 60%;
        left:61%;
    }
}

