.state-pane{
    display: inline-block;
    text-align: left;
    position:absolute;
    top:50%;
    left: 75%;
    color: aliceblue;
}

@media screen and (orientation: portrait) {
    .state-pane{
        display: inline-block;
        font-size: 2.2em;
        text-align: center;
        position:absolute;
        top: 60vh;
        left: 50%;
        transform: translate(-50%, 0);
        color: aliceblue;
    }
}